import React, { Component } from 'react'
import validator from 'validator'
import { toast } from 'react-toastify'
import HandSideImage from './hand-side.png'
import './Home.css'

export default class Home extends Component {
  state = {
    mailInput: '',
    fetching: false,
    submitted: false
  }

  handleMailChange = event => {
    this.setState({ mailInput: event.target.value})
  }

  handleMailSubmit = event => {
    event.preventDefault()
    const { mailInput } = this.state

    if(!validator.isEmail(mailInput)) {
      return toast.error('Please enter a valid email.')
    }

    this.setState({ fetching: true })
    fetch('/subscribe', {
      method: 'POST',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        mail: mailInput
      })
    })
    .then(response => response.json())
    .then(result => {
      this.setState({ mailInput: '', fetching: false, submitted: true })
    })
    .catch(error => {
      this.setState({ mailInput: '', fetching: false })
      toast.error('An unexpected error occurred. Please try again.')
    })
  }
  render() {
    const { mailInput, fetching, submitted } = this.state

    return (
      <div>
        {submitted ?
          <div className='mail-container'>
            <div className='mail-text-container'>
              <h1 className='mail-heading'>Congratulations! You are now waitlisted! Download the Contractor app in the App Store today and use code SIGNUPFREE to get in right now!</h1>
            </div>
          </div>
          :
          <div className='mail-container'>
            <div className='mail-text-container'>
              <h1 className='mail-heading'>Join Team Valay!</h1>
              <p className='mail-text'>Valay is the home repair sharing-economy app taking the repair market by storm! Signup to become a Founding User or Contractor!</p>
            </div>
            <fieldset disabled={fetching}>
              <form onSubmit={this.handleMailSubmit}>
                <div className='mail-input-container'>
                  <input
                    className='mail-input'
                    value={mailInput}
                    onChange={this.handleMailChange}
                    placeholder='Email Address'
                    type='text'/>
                  <button type='submit' className='mail-btn'>
                    SUBMIT
                  </button>
                </div>
              </form>
            </fieldset>
          </div>
        }
        <img src={HandSideImage} className='hand-side-img'/>
      </div>
    )
  }
}



